import { NgModule } from "@angular/core";
import { PromotionInformationPage } from "./promotion-information";
import { IonicPageModule } from "ionic-angular";
import { ComponentsModule } from "@components/components.module";
import { DirectivesModule } from "@directives/directives.module";
import { PipesModule } from "@pipes/pipes.module";
import { NgxMaskModule } from "ngx-mask";

@NgModule({
  declarations: [PromotionInformationPage],
  imports: [
    IonicPageModule.forChild(PromotionInformationPage),
    ComponentsModule,
    DirectivesModule,
    PipesModule,
    NgxMaskModule
  ],
  entryComponents: []
})
export class PromotionInformationPageModule { }


