import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ViewChild } from "@angular/core";

import * as moment from "moment-mini";

import { AppConstants } from "@app/app.constants";
import { AppGlobals } from "@app/app.globals";
import { AppLocales } from "@app/app.locales";

import { MarsAuthService } from "@services/auth.service";
import { MarsInteractionService } from "@services/interaction.service";
import { MarsNavigationService } from "@services/navigation.service";

import { Backend } from "@backend/index";

import { App, Platform, NavController, NavParams, ViewController } from "ionic-angular";


@Component({
    selector: "page-promotion-information",
    templateUrl: "promotion-information.html",
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class PromotionInformationPage {

    navigationService: MarsNavigationService;
    translations: AppTranslations;
    promotionId: string;
    promotion: any;
    token: string;

    constructor(private platform: Platform,
        private app: App,
        private changeDetector: ChangeDetectorRef,
        private viewCtrl: ViewController,
        private navCtrl: NavController,
        private navParams: NavParams,
        private locales: AppLocales,
        private globals: AppGlobals,
        private authService: MarsAuthService,
        private interactionService: MarsInteractionService) {
        this.navigationService = new MarsNavigationService(this.app);
        this.navigationService.setNavCtrl(this.navCtrl);
        this.translations = this.locales.load();
    }

    ionViewCanEnter() {
        return this.navigationService.authCheck(PromotionInformationPage.name, ['merchant']);
    }

    ionViewWillEnter() {
        if (!MarsAuthService.hasRole('MERCHANT')) return;
        this.initialize();
    }

    async initialize() {
        if (MarsAuthService.isLoggedIn()) {
            this.token = MarsAuthService.getMarsToken();
            this.getPromotion();
        }
    }

    isActive(promotion) {
        return promotion && (promotion.status == "active");
    }

    isCreated(promotion) {
        return promotion && promotion._id;
    }

    async getPromotion() {
        try {
            this.promotionId = this.navParams.get("promotionId");
            if (this.promotionId && this.promotionId.length > 0) {
                const promotion = (await Backend.getPromotion({ xAccessToken: this.token, id: this.promotionId })).data;
                promotion.startsAt = new Date(promotion.startsAt);
                promotion.endsAt = new Date(promotion.endsAt);
                this.promotion = promotion;
            } else {
                const start = moment(new Date());
                const end = moment(new Date()).add(7, "days");
                this.promotion = {
                    startsAt: start.toDate().toLocaleDateString("pt-BR"),
                    endsAt: end.toDate().toLocaleDateString("pt-BR")
                };
            }
        } catch (e) {
            this.interactionService.alert(this.translations.server_failure);
        } finally {
            this.changeDetector.detectChanges();
        }
    }

    async save(promotion) {
        const spinner = this.interactionService.spinner({ content: this.translations.loading + "..." });
        try {
            let parsed = Object.assign({}, promotion); // Prevents unwanted updates on the view
            parsed.startsAt = moment(promotion.startsAt, "DD-MM-YYYY", "pt-BR").toDate();
            parsed.endsAt = moment(promotion.endsAt, "DD-MM-YYYY", "pt-BR").toDate();
            const saved = (await Backend.savePromotion({ xAccessToken: this.token, promotion: parsed })).data;
            this.dismiss({ promotion: saved });
        } catch (e) {
            this.interactionService.alert(this.translations.server_failure);
        } finally {
            spinner.dismiss();
            this.changeDetector.detectChanges();
        }
    }

    dismiss(data = {}) {
        this.viewCtrl.dismiss(data);
    }

    goToPromotionCategoriesPage(promotion) {
        this.navigationService.goTo("PromotionCategoriesPage", {
            category: "root",
            promotionId: promotion._id || ""
        });
    }

    redirectToHomePage() {
        this.navigationService.goToRootPage();
    }
}



